.chatbot-container {
  width: 90%;
  max-width: 600px;
  height: 80vh;
  margin: 0 auto;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
}

.chat-wg {
  position: fixed;
  top: 5px;
  right: 30px;
  border: none;
}

.chatbot-history {
  flex-grow: 1;
  overflow-y: auto;
  margin-bottom: 20px;
  padding-right: 5px;
}

.chatbot-history-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.chatbot-user-message {
  align-self: flex-end; /* Align user's messages to the right */
  background-color: #dcf8c6; /* WhatsApp-like user message color */
  border-radius: 10px 10px 0 10px; /* Rounded corners with a tail on the top left */
  padding: 10px;
  margin-top: 5px;
  max-width: 70%;
  text-align: right; /* Align text inside the bubble to the right */
  font-size: 14px;
  word-wrap: break-word;
}

.chatbot-bot-message {
  align-self: flex-start; /* Align bot's messages to the left */
  background-color: #f1f1f1; /* Light gray for bot messages */
  border-radius: 10px 10px 10px 0; /* Rounded corners with a tail on the bottom right */
  padding: 10px;
  margin-top: 5px;
  max-width: 70%;
  text-align: left; /* Align text inside the bubble to the left */
  font-size: 14px;
  word-wrap: break-word;
}

.chatbot-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
}

.chatbot-input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  resize: none;
  box-sizing: border-box;
}

.chatbot-button {
  padding: 10px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 20px;
}

.chatbot-button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.chatbot-button:hover:enabled {
  background-color: #0056b3;
}
